.sv_progress-buttons__list-container .sv_progress-buttons__list li::before {
    border: 3px solid #e9c46a;
    background-color: #e9c46a;
}

.sv_progress-buttons__list-container .sv_progress-buttons__list li::after {
    background-color: #e9c46a;
}

.sv_progress-buttons__list-container .sv_progress-buttons__list li.sv_progress-buttons__list-element--current::before {
    border-color: #e76f51;
    background-color: #e9c46a;
}

.sv_progress-buttons__list-container .sv_progress-buttons__list li.sv_progress-buttons__list-element--passed::before {
    border: 3px solid #2a9d8f;
    background-color: #2a9d8f;
}

.sv_progress-buttons__list-container 
.sv_progress-buttons__list li.sv_progress-buttons__list-element--passed + li::after {
    background-color: #2a9d8f;
}

.sv_progress-buttons__list li.sv_progress-buttons__list-element--passed.sv_progress-buttons__list-element--current::before {
    background-color: #2a9d8f !important;
    border: 3px solid #e76f51 !important;

}

.sv-rating__item--selected .sv-rating__item-text:hover {
    background-color: #e9c46a;
}

.sv-rating input:focus + .sv-rating__min-text + .sv-rating__item-text, .sv-rating input:focus + .sv-rating__item-text {
    outline: 1px solid #e9c46a;
    outline-offset: 2px;
}

