    font-size: xx-large;
    padding-top: 2em;
    padding-bottom: 0.5em;
}

.MuiAccordionSummary-root {
    height: 15em;
    padding: 16px !important;
}

.MuiAccordionSummary-content {
    height: 100%;
    width: 100%;
}

.MuiAccordion-root.Mui-expanded {
    margin: 1em 0 0 0;
}

.MuiAccordionDetails-root {
    padding: 8px 16px 16px;
    height: 10em;
    display: flex;
    padding: 1em;
    justify-content: space-around; /* align horizontal */
    align-items: center; /* align vertical */
}

.accordionSummary {
    display:grid;
    height: 100%;
    width: 100%;
    grid-template-columns: 30% 35% 35%;
    grid-template-rows: auto 40% 40%;
    font-family: "Raleway";
}

.item-a {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 1;
    display: flex;
    justify-content: center; /* align horizontal */
    align-items: center; /* align vertical */
    font-size: 1.5vw;
    overflow: hidden;
    white-space: nowrap;
}

.item-values, .item-b, .item-c, .item-d, .item-e, .item-f, .item-g{
    display: flex;
    padding: 1em;
    padding-left: 3em;
    justify-content: space-between; /* align horizontal */
    align-items: center; /* align vertical */
}

.item-b {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 1;
}

.accordionSummary .MuiSvgIcon-root {
    fill: #264653 !important;
}

.item-values{
    display: flex;
